//SCSS
import '../scss/main.scss';
//Vendor-scripts
import './vendor/slick.min.js'
import './vendor/jquery.fancybox.min.js'
import './vendor/jQuery.maskedinput.min.js'
import './vendor/jquery-ui.min.js'
//Bootstrap
// import 'bootstrap';

//HTML
import '../../index.html';

$(document).ready(function () {
    $('.header__burger').on('click', function(){
        if($(this).hasClass('_active')) {
            $(this).removeClass('_active');
            $('.header__burger__list-wrap').removeClass('_active').slideUp(300);
        } else {
            $(this).addClass('_active');
            $('.header__burger__list-wrap').addClass('_active').slideDown(300);
        }
    });
});